.Switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  -webkit-tap-highlight-color: transparent; }

.Switch input {
  opacity: 0; }

.Slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.Slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

input:checked + .Slider {
  background-color: #54cf69; }

input:focus + .Slider {
  -webkit-box-shadow: 0 0 4px #54cf69;
          box-shadow: 0 0 4px #54cf69; }

input:checked + .Slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

/* Rounded sliders */
.Slider--round {
  border-radius: 34px; }

.Slider--round:before {
  border-radius: 50%; }



